@media (max-width: 768px) {
}

.main {
  position: fixed;
  top: 0;
  display: flex;
  flex-direction: row;
  height: 3rem;
  background-color: whitesmoke;
  border: none;
  width: 100%;
}

.header {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 0 1rem;
}

.body {
  padding: 0.3rem;
  display: flex;
  align-items: center;
  flex-grow: 2;
}

.body input {
  border: transparent;
  background-color: white;
  border-radius: 50rem;
  font-size: 1rem;
  padding-left: 2.5rem;
  width: 100%;
  height: 2rem;
}

.footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-grow: 1;
  justify-content: flex-end;
  font-size: 1.5rem;
}
.footer * {
  margin-right: 0.5rem;
}

.login_button {
  border-radius: 5rem;
  font-size: 0.7rem;
  border: none;
  width: 3rem;
  padding: 0.4rem;
  background-color: gainsboro;
}
.list {
  position: fixed;
  top: 3rem;
  right: 0;
  display: flex;
  flex-direction: column;
  height: auto;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.list div {
  font-size: 1rem;
  margin: 1rem;
}



.body .icon_search {
  position: absolute;
  font-size: 1.5rem;
  margin-left: 0.5rem;
}